#career {
  margin-left: 100px;
}
#career div{
  width: 1600px;
  
}

#career h1 {
  font-family: "SF_HambakSnow";
  color: #212a3e;
  font-size: 1.3em;
}
#career h3 {
  font-size: 1em;
  margin-bottom: 0px;
  margin-left: 30px;
}
#career h5 {
  font-size: 0.7em;
  color: gray;
  margin-top: 0px;
  margin-left: 30px;
}
#upmu{
  margin:0px;
  margin-left: 50px;
  font-size: 0.9em;
}
#upmuli{
  margin-left: 50px;
  font-size: 0.9em;
}
#career fieldset {
  width: 1550px;
  margin-left: 40px;
  background-color: white;
  font-size: 1em;
  
}

#career legend {
  color: white;
  background-color: #212a3e;
  padding-left: 40px;
  padding-right: 40px;
  padding-top: 20px;
  font-size: 0.8em;
  
}
#aaa {
  font-size : 0.9em;
  
}
#loyalty {
  margin-left : 600px;
  margin-bottom:50px;
  width: 400px;
  height: auto; 
}


#career ul {
  margin-left: 50px;
  font-size: 0.8em;
}

#career .ctn {
  display: flex;
  flex-wrap: nowrap;
  margin-bottom: 50px;

}
#career .it {
  width: 750px;
  height: 250px;
  margin-right: 50px;
  padding-top: 20px;
  text-align: center;
  border-style: dotted;
  border-color: rgb(228, 190, 0);
  border-radius: 40px;
  font-size: 0.7em;
  line-height: 0.8;
}

#career .ctn h3 {
  color: #032162;
  font-weight: bolder;
  font-size: 1.1em;
}
#career .ctn p {
  color: #ca9b00;
  font-size: 0.9em;
}
.App{
  display: flex;
  padding: 10px;
}

.chart{
  font-size: 0.9em;
  margin-left: 100px;
  display: flex;
  width: 1800px;
  justify-content: space-around;
}
.ch1{
  color : #07f;
}
