#project {
  margin-left: 100px;
}

#project h1 {
  font-family: "SF_HambakSnow";
  color: #212a3e;
  font-size: 1.1em;
}

#project img {
  /* margin-left:10px; */
  width: 800px;
  height: auto;
  box-shadow: 5px 5px 5px #606060;
}

#project fieldset {
  width: 1550px;
  margin-left: 30px;
  background-color: white;
  font-size: 0.8em;
}

#project legend {
  color: white;
  background-color: #212a3e;
  padding-left: 40px;
  padding-right: 40px;
  padding-top: 20px;
  font-size: 1em;
}

#project ol {
  margin-left: 40px;
  font-size: 0.9em;
  padding-right : 40px;
}
#project ul{
  /* margin-left : 50px; */
  font-size: 0.8em;
}

#mouse {
  color: gray;
  font-size: 0.7em;
}
#sp{
  margin : 20px;
}
#sp img {
  transition: all 0.2s linear;
}
#sp img:hover {
  transform: scale(1.3);
}
#group{
  margin : 30px;
  margin-bottom: 70px;
}