@import url("https://fonts.googleapis.com/css2?family=Song+Myung&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nanum+Pen+Script&display=swap");

@font-face {
  font-family: 'GangwonEdu_OTFBoldA';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2201-2@1.0/GangwonEdu_OTFBoldA.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'SF_HambakSnow';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2106@1.1/SF_HambakSnow.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

body {
  font-family: "GangwonEdu_OTFBoldA";
  background-color: #F1F6F9;
  line-height: 1.5;
  font-weight: bolder;
  /* margin-left : 50px; */
  }
header {
  font-family: "SF_HambakSnow";
  /* text-align: center; */
  font-size: 120px;
  margin: 100px;
  margin-bottom: 200px;
  line-height: 0.8
}
#headP{
  font-size: 45px;
  color: #7b88a4;
}
#headP2{
  font-size: 60px;
  color: #485571;
}

.container {
  display: grid;
  grid-template-columns: 450px 1fr;
}
nav {
  font-family: "SF_HambakSnow";
  width: 450px;
  margin-top: 18px;
  margin-left: 50px;
  font-size: 40px;
  background-color: #9da7bc;
}
main{
  font-family: "GangwonEdu_OTFBoldA";
  margin-left: 20px;
  background-color: #F1F6F9;
  font-size: 70px;
}
footer {
  font-family: "GangwonEdu_OTFBoldA";
  text-align: center;
  padding : 15px;
  background-color: #737b88;
  color: white;
  font-size: 35px;
  margin-top: 100px;
}
a:link:not(.headL), a:visited {
  background-color:#9da7bc; 
  color: #212A3E;
  padding: 10px 20px;
  text-decoration: none;
  display: inline-block;
  width:450px;
  font-size: 1.2em;
}
.headL{
  text-decoration: none;
}

a:hover:not(.headL), a:active:not(.headL) { 
  background-color: #212A3E; 
  color:white ;
}

button {
  font-family: "GangwonEdu_OTFBoldA";
  background-color: #2b5790;
  color: white ;
  padding: 10px 20px;
  margin-right: 30px; 
  font-size: 0.7em;
  cursor: pointer;
  transition-duration: 0.5s;
}
button:hover {
  background-color: #ffd429;
  color: white;
}
#category{
  margin: 0px;
  margin-left : 15px;
  color : white;
}
